import "./Products.css";
// import ico from "../../img/idea.png";

import React, { useState, useEffect } from "react";

import Categories from "./Categories/Categories";
import ProductsList from "./ProductsList/ProductsList";

const catURL = "https://fakestoreapi.com/products/categories";
const prodURL = "https://fakestoreapi.com/products/category";

const Products = () => {
  const [categories, setCategories] = useState(["..wait"]);
  const [activeCategory, setActiveCategory] = useState(-1);
  const [products, setProducts] = useState(["..wait"]);

  const getCatData = () => {
    if (categories.length === 1) {
      fetch(catURL)
        .then((res) => res.json())
        .then((res) => setCategories(res));
      //   .then((json) => console.log(json));
    }
  };

  const getProdData = (cat) => {
    fetch(prodURL + "/" + cat)
      .then((res) => res.json())
      .then((res) => setProducts(res));
    //   .then((json) => console.log(json));
  };

  useEffect(() => {
    getCatData();
  }, []);

  useEffect(() => {
    getProdData(categories[activeCategory]);
  }, [activeCategory]);

  return (
    <>
      <div className="products">
        <Categories
          categories={categories}
          actCat={activeCategory}
          setActCat={setActiveCategory}
        />
        {activeCategory !== -1 ? (
          <ProductsList products={products} />
        ) : (
          <p>..select CATEGORY!</p>
        )}
      </div>
    </>
  );
};

export default Products;
