// import logo from './logo.svg';
import "./App.css";

import { CartProvider } from "./components/CartContext";
import HeadMenu from "./components/HeadMenu/HeadMenu";
import Header from "./components/Header/Header";
import MainMenu from "./components/MainMenu/MainMenu";
import Footer from "./components/Footer/Footer";

import Home from "./sitesContent/Home/Home";
import Products from "./sitesContent/Products/Products";
import ProductDetail from "./sitesContent/Products/ProductDetail/ProduktDetail";
import AboutUs from "./sitesContent/AboutUs/AboutUs";
import Contact from "./sitesContent/Contact/Contact";
import Cart from "./sitesContent/Cart/Cart";

import React from "react";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

function App() {
  return (
    <CartProvider>
      <Router basename={process.env.PUBLIC_URL}>
        <div className="app">
          <HeadMenu />
          <Header />
          <MainMenu />
          <main>
            <Switch>
              <Route exact path="/">
                <Home />
              </Route>
              <Route path="/products">
                <Products />
              </Route>
              <Route path="/product/:id" component={ProductDetail}>
                {/* <ProductDetail /> */}
              </Route>
              <Route path="/aboutUs">
                <AboutUs />
              </Route>
              <Route path="/contact">
                <Contact />
              </Route>
              <Route path="/cart">
                <Cart />
              </Route>
            </Switch>
          </main>
          <Footer />
        </div>
      </Router>
    </CartProvider>
  );
}

export default App;
