import "./Categories.css";

const Categories = ({ categories, actCat, setActCat }) => {
  return (
    <div className="categories">
      <p>Categories:</p>
      <ul>
        {categories.map((item) => (
          <li
            className={categories.indexOf(item) === actCat ? "active" : ""}
            key={categories.indexOf(item)}
            onClick={() => setActCat(categories.indexOf(item))}
          >
            {item}
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Categories;
